import React from "react";
import { Link } from "react-router-dom";
import BannerImage from "../assets/batagor13.jpg";
import "../styles/Home.css";
function Home() {
  return (

    <div className="home" style={{ backgroundImage: `url(${BannerImage})` }}>
      <div className="headerContainer">
        <h1> A'a Diky Kitchen </h1>
        <p> Siomay dan Aneka Makanan</p>
        <Link to="/menu">
          <button> Lihat Menu dan Pesan </button>
        </Link>
      </div>
    </div>
  );
}

export default Home;
