import React, { Component } from 'react';
import {
  WhatsappShareButton,
  WhatsappIcon
  } from "react-share";

export default class ShareFb extends Component {
  render() {
    const shareUrl="https://wa.me/qr/VU3WSUMJOR5HN1"
    return (
        
      <div style={{
        height: "40px",
        width:'40px',
      }}>
        <WhatsappShareButton url={shareUrl}>
          <WhatsappIcon />
        </WhatsappShareButton>
      </div>
    )
  }
}
