import React from "react";
import PizzaLeft from "../assets/batagorContact.jpg";
import InstagramButton from "../helpers/WaButton";
import "../styles/Contact.css";

function Contact() {
  return (
    <div className="contact">
      <div
        className="leftSide"
        style={{ backgroundImage: `url(${PizzaLeft})` }}
      ></div>
      <div className="rightSide">
        <h1> Hubungi Kami</h1>

        <form id="contact-form" method="POST">
          <label htmlFor="name">Nama Lengkap</label>
          <input name="name" placeholder="Isi Nama Lengkap..." type="text" />
          <label htmlFor="email">Email</label>
          <input name="email" placeholder="Isi Email..." type="email" />
          <label htmlFor="message">Keperluan</label>
          <textarea
            rows="6"
            placeholder="Jelaskan Keperluan kalian disini..."
            name="message"
            required
          ></textarea>
          <InstagramButton/>
        </form>

      </div>
    </div>
  );
}

export default Contact;
