import React from "react";
import MultiplePizzas from "../assets/friedChicken.jpg";
import "../styles/About.css";
function About() {
  return (
    <div className="about">
      <div
        className="aboutTop"
        style={{ backgroundImage: `url(${MultiplePizzas})` }}
      ></div>
      <div className="aboutBottom">
        <h1> Dapoer A'a Diky</h1>
        <p>
        Dapoer A'a Diky menyediakan berbagai macam makanan olahan ikan dan ayam yang dipadukan dengan resep dan bahan fresh lainnya. 
        Menu kami mulai dari Siomay kukus, Siomay Goreng, Batagor, Batagor Kuah, Fried Chicken, Ayam geprek, Pempek Kapal selam,
        Pempek Adaan, hingga Pempek Kulit. Kami juga menyediakan jasa penyedian makanan untuk berbagai macam acara, seperti pernikahan, acara kantor, hingga acara keluarga.
        </p>
      </div>
    </div>
  );
}

export default About;
